import { stringifyUrl } from 'query-string';
export function makeUrl(url, data) {
    var result = {};
    if (data.fetchCount) {
        result.fetchCount = true;
    }
    if (data.filter) {
        Object.keys(data.filter).forEach(function (field) {
            result["filter[" + field + "]"] = data.filter[field];
        });
    }
    if (data.paginate) {
        Object.keys(data.paginate).forEach(function (field) {
            result["paginate[" + field + "]"] = data.paginate[field];
        });
    }
    if (data.sort) {
        Object.keys(data.sort).forEach(function (field) {
            result["sort[" + field + "]"] = data.sort[field];
        });
    }
    return stringifyUrl({ url: url, query: result }, { arrayFormat: 'bracket' });
}
